<script setup>
import { ref, onMounted, defineEmits, defineProps, watch } from 'vue';
import moment from 'moment'
import util from '../../utils/util.mjs';

    const emits = defineEmits(['emitSelect', 'emitMonth'])
    const props = defineProps({
        list: Array,
        firstMonth: Number,
        lastMonth: Number,
        month: Number,
        year: Number
    })
    const today = ref(moment())
    const days = ref(['일', '월', '화', '수', '목', '금', '토'])
    const thisYear = ref(moment(today.value).year())
    const thisMonth = ref(moment(today.value).month() + 1)
    const monthlyArr = ref([])
    watch(
        () => props.month,
        () => {
            thisMonth.value = props.month
            thisYear.value = props.year
            loadData()
        }
    )
    const loadData = () => {
        const arr = []
        const thisYearMonth = `${thisYear.value}-${String(thisMonth.value).padStart(2, '0')}-01`
        const startDay = moment(thisYearMonth).startOf('month').format('YYYY-MM-DD')
        const endDay = moment(thisYearMonth).endOf('month').format('YYYY-MM-DD')
        for (let i = 0; i < moment(endDay).format('DD'); i+= 1) {
            const day = (moment(startDay).format('DD') + i) % 7
            arr.push({
                date: i + 1,
                day
            })
        }
        if (moment(startDay).day() > 0) {
            const num = Number(moment(startDay).day())
            for (let j = 0; j < num; j += 1) {
                arr.unshift({
                    date: null,
                    day: num - (j + 1)
                })
            }
        }
        if (moment(endDay).day() < 6) {
            const num = Number(6 - moment(endDay).day())
            for (let h = 0; h < num; h += 1) {
                arr.push({
                    date: null,
                    day: h + 1
                })
            }
        }
        monthlyArr.value = arr
    }

    const nextMonth = () => {
        if (props.lastMonth && thisMonth.value === props.lastMonth) {
            return
        }
        if (thisMonth.value === 11) {
            thisMonth.value = 0
            thisYear.value++
        } else thisMonth.value++
        emits('emitMonth', thisMonth.value )
        loadData()
    }
    const prevMonth = () => {
        if (props.firstMonth && thisMonth.value === props.firstMonth) {
            return
        }
        if (thisMonth.value === 0) {
            thisMonth.value = 11
            thisYear.value--
        } else thisMonth.value--
        emits('emitMonth', thisMonth.value)
        loadData()
    }
    const emitMark = (date) => {
        if (props.month) {
            return
        }
        if (date) {
            const mark = `${thisYear.value}-${String(thisMonth.value).padStart(2, '0')}-${String(date).padStart(2, '0')}`
            emits('emitSelect', mark)
        }
    }
    onMounted(() => {
        loadData()
    })
</script>
<template>
    <div id="datePicker">
        <div class="title">
            <p @click="prevMonth()" class="arrow"
            :class="{gray: props.firstMonth && thisMonth === props.firstMonth}">
                &#x25C0;
            </p>
            <p>{{ `${thisYear}.${thisMonth}` }}</p>
            <p @click="nextMonth()" class="arrow"
            :class="{gray: props.lastMonth && thisMonth === props.lastMonth}">
                &#x25B6;
            </p>
        </div>
        <ul class="day-of-week">
            <li v-for="(day, dIdx) in days" :key="dIdx">
                {{ day }}
            </li>
        </ul>
        <ul class="day">
            <li  v-for="(dObj, dIdx) in monthlyArr" :key="dIdx"
            @click="emitMark(dObj.date)"
            class="date"
            :class="{on: props.list && props.list.findIndex((x) => moment(x.date).date() === dObj.date) > -1}">
                <span>{{ dObj.date }}</span>
                <template v-if="props.list">
                    <p v-if="props.list.findIndex((x) => moment(x.date).date() === dObj.date) > -1">
                        {{ util.priceKo(props.list.find((x) => moment(x.date).date() === dObj.date).weeklyPay) }}
                    </p>
                </template>
            </li>
        </ul>
    </div>
</template>
