<script setup>
import { defineProps, ref, watch, onMounted } from 'vue';
import moment from 'moment'
import DatePicker from './DatePicker.vue';
import util from '../../utils/util.mjs';

const props = defineProps({
    payList: Array
})
const month = ref(null)
const year = ref(null)
const firstMonth = ref(null)
const lastMonth = ref(null)
const monthlyPay = ref(0)
const weeklyList = ref([])
watch(
  () => props.payList,
  () => {
    firstMonth.value = moment(props.payList[0].date).month() + 1
    lastMonth.value = moment(props.payList[25].date).month() + 1
    month.value = moment(props.payList[0].date).month() + 1
    year.value = moment(props.payList[0].date).year()
    setData()
  }
)
const setData = () => {
    weeklyList.value = props.payList.filter((x) => moment(x.date).month() + 1 === month.value)
    monthlyPay.value = 0
    weeklyList.value.forEach((wObj) => {
        monthlyPay.value += wObj.weeklyPay
    })
}

const emitMonth = (val) => {
    month.value = val
    setData()
}
onMounted(() => {
    setData()
})

</script>
<template>
    <article id="calendar" class="article">
        <div class="max-width content">
            <div class="context">
                <h1><span>달력</span>으로 보는<br><span>납입 금액</span></h1>
                <div>
                    <h2>
                        <span>{{ month }}월</span> 납입 금액: <span>{{ util.priceKo(monthlyPay) }}</span>원
                    </h2> 
                    <ul>
                        <li v-for="(pay, pIdx) in payList.filter((x) => moment(x.date).month() + 1 === month)"
                        :key="pIdx">
                            <p>
                                {{ pay.weekly }}주차
                                <span class="date">{{ `${moment(pay.date).month() + 1}/${moment(pay.date).date()}` }}</span>
                            </p>
                            <span class="pay">{{ util.priceKo(pay.weeklyPay) }}원</span>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="calendar-outer">
                <DatePicker
                :list="weeklyList"
                :firstMonth="firstMonth"
                :lastMonth="lastMonth"
                :month="month"
                :year="year"
                @emitMonth="emitMonth" />
            </div>
        </div>
    </article>
</template>

