<script setup>
import { ref, onMounted, defineEmits } from 'vue';
const emits = defineEmits(['emitTab'])

let tab = ref(1000)
const tabArr = ref([
    { label: '1천원', value: 1000 },
    { label: '2천원', value: 2000 },
    { label: '3천원', value: 3000 },
    { label: '5천원', value: 5000 },
    { label: '1만원', value: 10000 },
])
const emitTab = (val) => {
    tab.value = val
    emits('emitTab', tab.value)
}
onMounted(() => {
    emits('emitTab', tab.value)
})
</script>x
<template>
    <div class="tab-menu">
        <p>최초 가입 금액</p>
        <ul>
            <li v-for="(tObj, tIdx) in tabArr" :key="tIdx">
                <button @click="emitTab(tObj.value)"
                :class="tab === tObj.value ? 'on' : ''">
                {{ tObj.label }}
                </button>
            </li>
        </ul>
    </div>
</template>
