<script setup>
  import { ref, defineEmits, watch } from 'vue';
  import moment from 'moment'
  import TabMenu from './TabMenu.vue';
  import DatePicker from '../DatePicker.vue';
  const emits = defineEmits(['emitData'])

  let tab
  const date = ref(moment())
 
  const openDatePicker = ref(false)

  const emitTab = (val) => {
    tab = val
    emits('emitData', tab, date.value)    
  }
  const emitSelect = (val) => {
    if (val) date.value = val
    emits('emitData', tab, date.value)
  }

  watch(date, () => {
    openDatePicker.value = !openDatePicker.value
  })
</script>
<template>
  <article id="intro" class="max-width article">
    <h1>카카오뱅크&#x1F4B0;<span>26주 적금</span> 계산기</h1>
    <TabMenu @emitTab="emitTab"/>
    <div class="pick-date">
      <ul class="depth1">
        <li class="join-date">
          <ul class="depth2">
            <li>
              <p>최초 가입일</p>
            </li>
            <li class="date-input">
              <div class="date-text" @click="openDatePicker = !openDatePicker">
                <p>{{ moment(date).format('YYYY-MM-DD') }}</p>
                <span>&#x1F4C5;</span>
              </div>
              <div
                v-if="openDatePicker === true"
                class="date-picker">
                <DatePicker
                  @emitSelect="emitSelect" />
              </div>
            </li>
          </ul>
        </li>
        <li>
          <ul class="depth2">
              <li>
                <p>최고 금리</p>
                <p>23.04.19 기준</p>
              </li>
              <li class="benefit">연 7.00%</li>
          </ul>
        </li>
      </ul>
    </div>
  </article>
</template>
